@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.App{padding-left:10px;margin-top:10px}.App .form-container{width:350px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:10px;border:solid 1px #000}.App .form-container .form-label{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-bottom:20px;font-size:20px}.App .form-container .form-input{width:200px;margin-top:5px;padding:3px;font-size:14px;border:solid 1px #c1c1c1}.App .form-container .form-submit{width:100px;border:solid 1px #c1c1c1;border-radius:4px;background-color:transparent;padding:3px;font-size:14px;cursor:pointer}.App .form-container .form-submit:hover,.App .form-container .form-submit:focus{background-color:#3d7de7}.App .filter-label{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;font-size:16px}.App .filter-input{width:200px;margin-top:5px;padding:3px;border:solid 1px #c1c1c1}.App .contact-list .contact-list-item{font-size:16px}.App .contact-list .contact-list-item:not(:last-child){margin-bottom:15px}.App .contact-list .contact-list-item .contact-name{margin-right:10px}.App .contact-list .contact-list-item .contact-number{margin-right:25px}
